.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loggedinUser {
  padding:4px;  
  font-weight: bold;
}

.smaller-input {
  font-size: 12px;
  padding: 2px 5px;
}

.content {
  /* padding-top: 70px; */
  min-height: calc(100vh - 150px);  
}

.footer {
  color: brown;
  font-Style: 'italic';
  font-weight: bold;
  font-size: 16;
  text-align: right
}

.navbar {
  background-color: goldenrod;
  font-size: 10;
  font-weight: bold;
}

.components {
  /* font-style: oblique; */
  background-color:lightsalmon
}

.bold-text {
  font-weight: bold;
}